    margin-bottom: 10px; /* Adiciona um espa√ßo entre o QR Code e o conte√∫do abaixo */
    justify-content: center;
    display: flex;
    padding: 0;
}

.areaqr {
    margin: 0 auto;
    padding: 0;
}
.pdf-element .qr-code {
    margin-top: -72px; /* Reduz a margem superior para 0 */
    margin-bottom: -12px; /* Reduz a margem superior para 0 */
}
.pdf-element .qr-code img{
